.header {

}

.header__wrapper {
  box-sizing: border-box;
  padding: 10px 0;
  display: flex;
  align-items: center;
}

.header__logo {
  margin-left: 0;
  margin-right: auto;
  width: 100px;
}

.header__nav-bar {
  margin-right: 0;
  margin-left: auto;
  & > * + * {
    margin-left: 20px;
  }
}